import {
  Box,
  IconButton,
  Button,
  HStack,
  Menu,
  MenuItem,
  MenuButton,
  MenuList,
  Flex,
} from "@chakra-ui/react";

import { Link } from "react-router-dom";

import { useSelector, useDispatch } from "react-redux";
import { logoutRequest } from "../../state/userReducer";

import { AiOutlineShoppingCart } from "react-icons/ai";
import { FaUserCog } from "react-icons/fa";

export default function AccountButtons({ mt }) {
  const user = useSelector((state) => state.user);
  const dispatch = useDispatch();
  const handleLogout = () => {
    dispatch(logoutRequest());
  };

  return (
    <Flex mt={mt ? mt : 0} >
      {user?.id ? (
        user.isAdmin ? (
          <HStack>
            <Box zIndex="9999">
              {user.isSuperAdmin ? (
                <Menu>
                  <MenuButton
                    variant="solid"
                    bg="black"
                    as={Button}
                    icon={<FaUserCog />}
                  >
                    SuperAdmin
                  </MenuButton>
                  <MenuList bg="black">
                    <Link to="/admin/users">
                      <MenuItem>Set Users</MenuItem>
                    </Link>

                    <Link to="/admin/categories">
                      <MenuItem>Set Categories</MenuItem>
                    </Link>
                    <Link to="/admin/productslist">
                      <MenuItem>Set Products</MenuItem>
                    </Link>
                  </MenuList>
                </Menu>
              ) : (
                <Menu>
                  <MenuButton as={Button} icon={<FaUserCog />}>
                    Admin
                  </MenuButton>
                  <MenuList>
                    <Link to="/admin/categories">
                      <MenuItem>Set Categories</MenuItem>
                    </Link>
                    <Link to="/admin/productslist">
                      <MenuItem>Set Products</MenuItem>
                    </Link>
                  </MenuList>
                </Menu>
              )}
            </Box>
            <Link to="/">
              <Button
                ml={5}
                mr={3}
                variant="link"
                colorScheme="yellow"
                onClick={handleLogout}
              >
                Logout
              </Button>
            </Link>
          </HStack>
        ) : (
          <HStack>
            <Link to="/orders">
              <Button variant="link" colorScheme="yellow" p={3}>
                {user.userName}
              </Button>
            </Link>
            <Link to="/cart">
              <IconButton
                color="#0c0f0a"
                bg="#ffcd1f"
                variant="solid"
                icon={<AiOutlineShoppingCart />}
              />
            </Link>
            <Link to="/">
              <Button
                variant="solid"
                bg="#ffcd1f"
                color="#0c0f0a"
                onClick={handleLogout}
              >
                Logout
              </Button>
            </Link>
          </HStack>
        )
      ) : (
        <>
          <Link to="/login">
            <Button variant="solid" color="#0c0f0a" bg="#ffcd1f" mr={1}>
              Login
            </Button>
          </Link>
          <Link to="/register">
            <Button variant="solid" mr={1} color="#0c0f0a" bg="#ffcd1f">
              Sign Up
            </Button>
          </Link>
          <Link to="/login">
            <IconButton
              mr={1}
              color="#0c0f0a"
              bg="#ffcd1f"
              variant="solid"
              icon={<AiOutlineShoppingCart />}
            />
          </Link>
        </>
      )}
    </Flex>
  );
}
