import React from "react";
import {
  Box,
  Button,
  HStack,
  Image,
  Stack,
  Text,
  useBreakpointValue,
} from "@chakra-ui/react";
import { Link } from "react-router-dom";
//a
function Card({ product }) {
  return (
    <Stack
      w={"60"}
      borderRadius="10"
      mt={2}
      bg={"black"}
      p="5"
      spacing={useBreakpointValue({
        base: "4",
        md: "5",
      })}
    >
      <Box
        position="relative"
        bg="white"
        borderRadius="10"
      >
        <Image
              rounded={"lg"}
              height={229}
              width={282}
              objectFit={"contain"}
              src={product.photo}
            />
      </Box>
      <Stack>
        <Stack spacing="1">
          <Text fontWeight="medium" color="white">
            {product.name}
          </Text>
          <Text fontWeight={800} fontSize={"xl"} color="#ffcd1f">
            $ {product.price}
          </Text>
        </Stack>
        <HStack></HStack>
      </Stack>
      <Stack align="center">
        <Link to={`/products/${product.id}`}>
          <Button colorScheme="yellow"isFullWidth>
            see more
          </Button>
        </Link>
      </Stack>
    </Stack>
  );
}

export default Card;

