import React, { useEffect } from "react";
import {
  chakra,
  Box,
  Link,
  Image,
  SimpleGrid,
  Text,
  useToast,
} from "@chakra-ui/react";
import { StarIcon } from "@chakra-ui/icons";
import axios from "axios";
import { useSelector } from "react-redux";
import { useParams } from "react-router-dom";
import CommentSection from "./CommentSection";

const CardProduct= () => {
  const { id } = useParams();
  const [product, setProduct] = React.useState({});
  const [valoration, setValoration] = React.useState({});
  const user = useSelector((state) => state.user);
  const toast = useToast();
  useEffect(() => {
    axios
      .get(`/api/products/id/${id}`)
      .then((resp) => resp.data)
      .then((data) => {
        setProduct(data.product);
        setValoration(data.valoration);
      });
  }, [id]);

  const addToCart = () => {
    return axios
      .post(`/api/cart`, { id: product.id, price: product.price })
      .then((resp) => {
        toast({
          title: "Product added ",
          description: "Your product has been added",
          status: "success",
          duration: 2000,
          isClosable: true,
        });
        return resp.data;
      });
  };

  return (
    <Box height="full">
      <SimpleGrid
        columns={2}
        minChildWidth="300px"
      >
        <Box bg={"black"} m="10">
          <Box alignItems="center">
            <Image
              mt="5"
              ml="25px"
              mr="10px"
              rounded={"lg"}
              height="full"
              width="90%"
              objectFit={"contain"}
              src={product.photo}
            />
          </Box>

          <Box ml="25px" py={12} px={6}>
            <chakra.h2
              fontSize={{ base: "2xl", md: "3xl" }}
              color="#ffcd1f"
              fontWeight="bold"
            >
              {product.name}
            </chakra.h2>
            <chakra.p mt={4} color="#ffcd1f">
              {product.description}
            </chakra.p>
            <chakra.h1 color="#ffcd1f" fontWeight="bold" fontSize="lg">
              USD {product.price}
            </chakra.h1>
            <chakra.h1 color="#ffcd1f" fontWeight="bold" fontSize="lg">
              Stock: {product.stock}
            </chakra.h1>
            <Box d="flex" mt="2" alignItems="center">
              {Array(5)
                .fill("")
                .map((_, i) => (
                  <StarIcon
                    key={i}
                    color={i <= valoration ? "teal.500" : "gray.300"}
                  />
                ))}
            </Box>

            {user?.id ? (
              <Box mt={8}>
                {product.stock > 0 ? (
                  <Link
                    onClick={addToCart}
                    bg="#FFCD1F"
                    color="black"
                    px={5}
                    py={3}
                    fontWeight="semibold"
                    rounded="lg"
                    _hover={{ bg: "gray.800" }}
                  >
                    Add to Cart
                  </Link>
                ) : (
                  <Text
                    bg="#FFCD1F"
                    color="black"
                    px={5}
                    py={3}
                    fontWeight="semibold"
                    rounded="lg"
                    textAlign={"center"}
                  >
                    SIN STOCK
                  </Text>
                )}
              </Box>
            ) : (
              <Box mt={8}>
                <Link
                  href="/login"
                  bg="gray.900"
                  color="gray.100"
                  px={5}
                  py={3}
                  fontWeight="semibold"
                  rounded="lg"
                  _hover={{ bg: "gray.800" }}
                >
                  Login
                </Link>
              </Box>
            )}
          </Box>
        </Box>

        <Box w="full" h="full" justifyContent={"center"} minChildWidth="300px">
          <CommentSection id={id} />
        </Box>
      </SimpleGrid>
    </Box>
  );
};

export default CardProduct;
