import React, { useEffect } from "react";
import axios from "axios";
import Card from "./Card";

import Carousel from "react-elastic-carousel";
import { Heading, Box } from "@chakra-ui/react";
const breakPoints = [
  { width: 1, itemsToShow: 1 },
  { width: 550, itemsToShow: 2, itemsToScroll: 2 },
  { width: 768, itemsToShow: 3 },
  { width: 1200, itemsToShow: 4 },
];
const ProductsGrid = () => {
  const [cuerda, setCuerda] = React.useState([]);
  const [viento, setViento] = React.useState([]);
  const [percusion, setPercusion] = React.useState([]);

  useEffect(() => {
    axios
      .get(`/api/products/category/cuerda`)
      .then((res) => res.data)
      .then((data) => {
        setCuerda(data);
      });
    axios
      .get(`/api/products/category/viento`)
      .then((res) => res.data)
      .then((data) => {
        setViento(data);
      });
    axios
      .get(`/api/products/category/percusion`)
      .then((res) => res.data)
      .then((data) => {
        setPercusion(data);
      });
  }, []);

  return (
    <Box bg="#e6e6e6">
      <Box marginTop={20}>
        <Heading
          textAlign="center"
          fontFamily="Helvetica"
          marginTop={5}
          marginBottom="20px"
        >
          <br />
          CUERDA
        </Heading>
        <Carousel breakPoints={breakPoints}>
          {cuerda.map((product, i) => {
            return <Card key={i} product={product} />;
          })}
        </Carousel>
      </Box>
      <>
        <Heading
          textAlign="center"
          marginTop="20px"
          marginBottom="20px"
          fontFamily={"body"}
        >
          <br />
          PERCUSION
        </Heading>
        <Carousel breakPoints={breakPoints}>
          {percusion.map((product, i) => {
            return <Card key={i} product={product} />;
          })}
        </Carousel>
      </>
      <>
        <Heading textAlign="center" marginTop="20px" marginBottom="20px">
          <br />
          VIENTO
        </Heading>
        <Carousel breakPoints={breakPoints}>
          {viento.map((product, i) => {
            return <Card key={i} product={product} />;
          })}
        </Carousel>
      </>
    </Box>
  );
};

export default ProductsGrid;
